import React, { Component } from "react";
import "./App.css";
import { Card, Menu } from "semantic-ui-react";
import NavBarContainer from "./components/NavBarContainer";
import Globals from "./Globals";
import HotelList from "./components/HotelList";

class App extends Component {
  state = {
    visible: false
  };

  titleCard = ({ text, id }) => (
    <Card
      color="green"
      fluid
      style={{ backgroundColor: Globals.COLOR_BIRU_MUDA }}
    >
      <Card.Content>
        <div id={id}>
          <h3 style={{ textAlign: "center", color: "white" }}>{text}</h3>
        </div>
      </Card.Content>
    </Card>
  );

  theGap = () => <div style={{ width: "100%", height: "3em" }} />;

  contactUs = () => (
    <Card fluid>
      <Card.Content>
        <div style={{ textAlign: "center" }}>
          <p>Office: JL. DR Sutomo No.27 Mataram - Lombok</p>
          <p
            style={{
              display: "flex",
              alignItems: "center",
              justifyContent: "center"
            }}
          >
            <img
              alt=""
              src="https://png.icons8.com/windows/50/000000/phone.png"
              style={{ height: 20, width: "auto", marginRight: 2 }}
            />
            +62 8190 7648 999
          </p>
          <p
            style={{
              display: "flex",
              alignItems: "center",
              justifyContent: "center"
            }}
          >
            <a href="https://wa.me/6281339680009">
              <img
                alt=""
                src="https://png.icons8.com/windows/50/000000/whatsapp.png"
                style={{ height: 20, width: "auto", marginRight: 2 }}
              />
              +62 8133 9680 009
            </a>
          </p>
          <p
            style={{
              display: "flex",
              alignItems: "center",
              justifyContent: "center"
            }}
          >
            <img
              alt=""
              src="https://png.icons8.com/windows/50/000000/new-post.png"
              style={{ height: 20, width: "auto", marginRight: 2 }}
            />
            lombokares@yahoo.co.id
          </p>
          <br />
          <br />
          <div style={{ display: "flex", justifyContent: "center" }}>
            <div style={{ width: 200 }}>
              Lalu Akram Wirahady, S.E.
              <hr />
              Director
            </div>
          </div>
        </div>
      </Card.Content>
    </Card>
  );

  products = () => (
    <Card fluid>
      <Card.Content>
        <div style={{ display: "flex", justifyContent: "center" }}>
          <ol>
            <li>
              <h4>Inbound and Outbond Tour Packages</h4>
            </li>
            <li>
              <h4>Hotel Reservation</h4>
            </li>
            <li>
              <h4>M.I.C.E - Holy Tour / Haji / Umroh</h4>
            </li>
          </ol>
        </div>
      </Card.Content>
    </Card>
  );

  render() {
    return (
      <React.Fragment>
        <NavBarContainer>
          <div className="app-container">
            <div className="app-content">
              <this.titleCard text="Our Products" />
              <this.products />

              <this.theGap />

              <this.titleCard text="Hotels" id="div-hotel" />
              <HotelList />

              <this.theGap />

              <this.titleCard text="Contact Us" id="div-contact" />
              <this.contactUs />
            </div>
          </div>
        </NavBarContainer>
        <Menu>
          <div style={{ margin: 10, width: "100%" }}>
            <p style={{ textAlign: "center" }}>
              <span style={{ color: Globals.COLOR_BIRU_TUA }}>
                &copy;&nbsp;&nbsp;
                {"2018 - Ares Tours & Travel"}
              </span>
            </p>
          </div>
        </Menu>
      </React.Fragment>
    );
  }
}

export default App;



