import React, { Component } from "react";
import {
  Icon,
  Modal,
  Image,
  Form,
  Header,
  Button,
  Card,
  Grid,
  Dimmer,
  Loader,
  Dropdown
} from "semantic-ui-react";
import Globals from "../Globals";
import Axios from "axios";

const initState = {
  modalHotel: false,
  modalSubmitSuccess: false,
  selectedHotelId: 0,
  fullname: "",
  email: "",
  phone: "",
  submitting: false,
  hotels: [],
  hotel_duration_default: "1 night",
  hotel_rooms_default: "1 room"
};

function formatRp(value) {
  var rupiah = "";
  var angkarev = value
    .toString()
    .split("")
    .reverse()
    .join("");
  for (var i = 0; i < angkarev.length; i++)
    if (i % 3 === 0) rupiah += angkarev.substr(i, 3) + ".";
  return rupiah
    .split("", rupiah.length - 1)
    .reverse()
    .join("");
}

class HotelList extends Component {
  state = initState;

  showModalHotel = id => {
    this.setState({
      modalHotel: true,
      selectedHotelId: id
    });
  };
  hideModalHotel = () =>
    this.setState({
      modalHotel: false,
      modalSubmitSuccess: false,
      selectedHotelId: 0,
      fullname: "",
      email: "",
      phone: "",
      submitting: false
    });

  hotelCard = ({ id, img, url, name, stars, location, price_idr }) => {
    var renderStars = [];
    for (var i = 0; i < stars; i++) {
      renderStars.push(
        <img
          key={i + 1}
          alt=""
          src="https://png.icons8.com/color/50/000000/filled-star.png"
          style={{ height: 25, width: "auto", marginRight: 3 }}
        />
      );
    }
    return (
      <Card
        fluid
        className="app-clickable"
        onClick={this.showModalHotel.bind(this, id)}
      >
        <Card.Content>
          <div style={{ display: "flex" }}>
            <img
              alt=""
              src={img}
              style={{ height: 150, width: 230, objectFit: "cover" }}
            />
            <div style={{ marginLeft: 10 }}>
              <span
                style={{ fontSize: 20, fontWeight: "bold", marginBottom: 12 }}
              >
                {name}
              </span>
              <div
                style={{ display: "flex", marginLeft: -2, marginBottom: 15 }}
              >
                {renderStars}
              </div>
              <div style={{ display: "flex" }}>
                <img
                  alt=""
                  src="https://png.icons8.com/metro/50/666666/marker.png"
                  style={{
                    height: 17,
                    width: "auto",
                    marginRight: 0,
                    marginTop: 3
                  }}
                />
                <p style={{ colod: "grey" }}>{location}</p>
              </div>
              <div style={{ marginLeft: 3, marginTop: 12 }}>
                <h3 style={{ color: Globals.COLOR_ORANYE }}>{`Rp ${formatRp(
                  price_idr
                )}`}</h3>
              </div>
            </div>
          </div>
        </Card.Content>
      </Card>
    );
  };

  onChangeForm = e => {
    var value = e.target.value;
    var name = e.target.name;
    this.setState({
      [name]: value
    });
  };

  getHotels = () => {
    var url = "https://rommyarbantas.net/arestour/api.php/get/hotels";
    Axios.post(url)
      .then(r => {
        if (typeof r.data === "object") {
          this.setState({
            hotels: r.data
          });
        } else {
          console.log(r.data);
        }
      })
      .catch(e => {
        console.error(e);
      });
  };

  _submit = () => {
    // check if form empty
    if (
      this.state.fullname === "" ||
      this.state.email === "" ||
      this.state.phone === ""
    ) {
      alert("Fields cannot be empty!");
      return false;
    }

    this.setState({ submitting: true });

    var number_rooms = parseInt(
      this.state.hotel_rooms_default.replace(/[^0-9]/g, ""),
      10
    );
    var number_duration = parseInt(
      this.state.hotel_duration_default.replace(/[^0-9]/g, ""),
      10
    );

    var url = "https://rommyarbantas.net/arestour/api.php/submithotelbook";
    var data = new FormData();
    data.append("fullname", this.state.fullname);
    data.append("email", this.state.email);
    data.append("phone", this.state.phone);
    data.append("id_hotel", this.state.selectedHotelId);
    data.append("rooms", number_rooms);
    data.append("duration", number_duration);
    Axios.post(url, data)
      .then(r => {
        if (r.data.success) {
          this.setState({
            modalSubmitSuccess: true,
            submitting: false
          });
        } else {
          console.log("Failed:");
          console.log(r.data);
        }
      })
      .catch(e => {
        this.setState({
          submitting: false
        });
      });
  };

  componentDidMount() {
    this.getHotels();
  }

  render() {
    let selectedHotelId = this.state.selectedHotelId;
    let img =
      this.state.hotels.find(item => item.id === selectedHotelId) !== undefined
        ? this.state.hotels.find(item => item.id === this.state.selectedHotelId)
            .img
        : null;
    // let url =
    //   this.state.hotels.find(item => item.id === selectedHotelId) !== undefined
    //     ? this.state.hotels.find(item => item.id === this.state.selectedHotelId)
    //         .url
    //     : null;
    let name =
      this.state.hotels.find(item => item.id === selectedHotelId) !== undefined
        ? this.state.hotels.find(item => item.id === this.state.selectedHotelId)
            .name
        : null;
    let stars =
      this.state.hotels.find(item => item.id === selectedHotelId) !== undefined
        ? this.state.hotels.find(item => item.id === this.state.selectedHotelId)
            .stars
        : null;
    let location =
      this.state.hotels.find(item => item.id === selectedHotelId) !== undefined
        ? this.state.hotels.find(item => item.id === this.state.selectedHotelId)
            .location
        : null;
    let price_idr =
      this.state.hotels.find(item => item.id === selectedHotelId) !== undefined
        ? this.state.hotels.find(item => item.id === this.state.selectedHotelId)
            .price_idr
        : null;

    var renderStars = [];
    for (var i = 0; i < stars; i++) {
      renderStars.push(
        <img
          key={i + 1}
          alt=""
          src="https://png.icons8.com/color/50/000000/filled-star.png"
          style={{ height: 25, width: "auto", marginRight: 3 }}
        />
      );
    }

    ////////////////////////////////////////////////////////////////////////

    let hotel_durations = [];
    let hotel_rooms = [];

    var max_duration = 15;
    var max_room = 25;

    for (var k = 0; k < max_duration; k++) {
      var duration_order = k + 1;
      var duration =
        k > 0 ? duration_order + " nights" : duration_order + " night";
      hotel_durations.push({
        key: duration_order,
        value: duration,
        text: duration
      });
    }
    for (var j = 0; j < max_room; j++) {
      var room_order = j + 1;
      var rooms = j > 0 ? room_order + " rooms" : room_order + " room";
      hotel_rooms.push({ key: room_order, value: rooms, text: rooms });
    }

    var number_rooms = parseInt(
      this.state.hotel_rooms_default.replace(/[^0-9]/g, ""),
      10
    );
    var number_duration = parseInt(
      this.state.hotel_duration_default.replace(/[^0-9]/g, ""),
      10
    );

    var price_idr_static = "";
    if (price_idr !== null) {
      price_idr_static = formatRp(price_idr);
      price_idr = formatRp(price_idr * number_rooms * number_duration);
    }

    return (
      <React.Fragment>
        {this.state.hotels.map((row, i) => (
          <this.hotelCard
            key={i}
            id={row.id}
            img={row.img}
            url={row.url}
            name={row.name}
            stars={row.stars}
            location={row.location}
            price_idr={row.price_idr}
          />
        ))}
        <Modal
          size="fullscreen"
          open={this.state.modalHotel}
          onClose={this.hideModalHotel}
        >
          <Modal.Content image>
            <Image wrapped size="large" src={img} />
            <Modal.Description>
              <div>
                <span
                  style={{
                    fontSize: 20,
                    fontWeight: "bold",
                    marginBottom: 12
                  }}
                >
                  {name}
                </span>
                <div
                  style={{
                    display: "flex",
                    marginLeft: -2,
                    marginBottom: 15
                  }}
                >
                  {renderStars}
                </div>
                <div style={{ display: "flex" }}>
                  <img
                    alt=""
                    src="https://png.icons8.com/metro/50/666666/marker.png"
                    style={{
                      height: 17,
                      width: "auto",
                      marginRight: 0,
                      marginTop: 3
                    }}
                  />
                  <p style={{ colod: "grey" }}>{location}</p>
                </div>
                <div style={{ marginLeft: 3, marginTop: 12 }}>
                  <h3
                    style={{ color: Globals.COLOR_ORANYE }}
                  >{`Rp ${price_idr_static}`}</h3>
                </div>
              </div>
              <br />
              {/* /// form di sini /// */}
              <hr />
              <h4>BOOK NOW</h4>

              <div style={{ display: "flex" }}>
                <Dropdown
                  selection
                  value={this.state.hotel_duration_default}
                  onChange={(e, data) =>
                    this.setState({ hotel_duration_default: data.value })
                  }
                  options={hotel_durations}
                />
                &nbsp;
                <Dropdown
                  selection
                  value={this.state.hotel_rooms_default}
                  onChange={(e, data) =>
                    this.setState({ hotel_rooms_default: data.value })
                  }
                  options={hotel_rooms}
                />
              </div>

              <br />

              <Form>
                <Form.Field>
                  <label>Fullname</label>
                  <input
                    name="fullname"
                    onChange={this.onChangeForm}
                    placeholder="Fullname"
                    value={this.state.fullname}
                  />
                </Form.Field>
                <Form.Field>
                  <label>Email address</label>
                  <input
                    name="email"
                    onChange={this.onChangeForm}
                    type="email"
                    placeholder="Email address"
                    value={this.state.email}
                  />
                </Form.Field>
                <Form.Field>
                  <label>Phone number</label>
                  <input
                    name="phone"
                    onChange={this.onChangeForm}
                    placeholder="Phone number"
                    value={this.state.phone}
                  />
                </Form.Field>
                <Card fluid color="green">
                  <Card.Content>
                    <Card.Header>Price details</Card.Header>
                    <Grid columns={2}>
                      <Grid.Row>
                        <Grid.Column>{`(${number_rooms}x) Room - ${
                          this.state.hotel_duration_default
                        }`}</Grid.Column>
                        <Grid.Column>{`Rp ${price_idr}`}</Grid.Column>
                      </Grid.Row>
                    </Grid>
                    <hr />
                    <b>Payment options:</b>
                    <br />
                    <br />
                    <Grid columns={2}>
                      <Grid.Row>
                        <Grid.Column>Transfer Bank BCA</Grid.Column>
                        <Grid.Column>
                          <img
                            alt=""
                            width="auto"
                            height={20}
                            src="https://ecs7.tokopedia.net/img/toppay/sprites/bca.png"
                          />
                          {/* <p>
                            BCA Cabang Cakranegara-Lombok
                            <br />
                            No Rek: 0560-676-824
                            <br />
                            a.n. Lalu Akram Wirahady
                          </p> */}
                        </Grid.Column>
                      </Grid.Row>
                      <Grid.Row>
                        <Grid.Column>Transfer Bank BRI</Grid.Column>
                        <Grid.Column>
                          <img
                            alt=""
                            width="auto"
                            height={20}
                            src="https://ecs7.tokopedia.net/img/toppay/sprites/bri.png"
                          />
                          {/* <p>
                            BRI KCP Sri Wijaya
                            <br />
                            No Rek: 2070-01-000214-56-1
                            <br />
                            a.n. CV Ares Pusaka Abadi
                          </p> */}
                        </Grid.Column>
                      </Grid.Row>
                      <Grid.Row>
                        <Grid.Column>Paypal</Grid.Column>
                        <Grid.Column>
                          <img
                            alt=""
                            width="auto"
                            height={30}
                            src="https://graffikgallery.co.uk/wp-content/uploads/2016/09/paypal-attend-graffik-gallery-corporate-graffiti-workshops.png"
                          />
                        </Grid.Column>
                      </Grid.Row>
                    </Grid>
                    <br />
                    <i>
                      <b>
                        Please check your email after submit. We will send you
                        information about the payment, and you can choose one of
                        the payment methods. Including PayPal.
                      </b>
                    </i>
                  </Card.Content>
                </Card>
                <Button positive type="button" onClick={this._submit}>
                  SUBMIT
                </Button>
              </Form>
              <Dimmer active={this.state.submitting}>
                <Loader size="massive">Submitting...</Loader>
              </Dimmer>
            </Modal.Description>
          </Modal.Content>
          <Modal.Actions>
            <Button content="Cancel" onClick={this.hideModalHotel} />
          </Modal.Actions>
        </Modal>

        {/* // MODAL SUBMIT SUCCESS // */}
        <Modal
          closeOnDimmerClick={false}
          open={this.state.modalSubmitSuccess}
          basic
          size="small"
          onClose={() => this.setState({ modalSubmitSuccess: false })}
        >
          <Header icon="check" content="Submitted" />
          <Modal.Content>
            <p>
              Your book request is submitted! Please check your email. If it's
              not in your Inbox, check SPAM folder.
            </p>
          </Modal.Content>
          <Modal.Actions>
            <Button
              color="green"
              inverted
              onClick={() =>
                this.setState(
                  {
                    modalSubmitSuccess: false
                  },
                  this.hideModalHotel
                )
              }
            >
              <Icon name="checkmark" /> OK
            </Button>
          </Modal.Actions>
        </Modal>
      </React.Fragment>
    );
  }
}

export default HotelList;



