// import _ from "lodash";
import React, { Component } from "react";
import {
  Container,
  Icon,
  Modal,
  Menu,
  Sidebar,
  Responsive,
  Button
} from "semantic-ui-react";
import logo from "../img/logo.png";
import logoMain from "../img/logo_main_2.png";
import imgBanner from "../img/banner.jpg";
import imgAsita from "../img/asita.png";
import imgWi from "../img/wi.png";

const leftItems = [
  {
    as: "a",
    content: "Hotel",
    key: "hotel",
    icon: (
      <img
        alt=""
        src="https://png.icons8.com/windows/50/000000/bed.png"
        style={{ height: 25, width: "auto", marginRight: 5 }}
      />
    )
  },
  {
    as: "a",
    content: "Products",
    key: "products",
    icon: (
      <img
        alt=""
        src="https://png.icons8.com/windows/50/000000/product.png"
        style={{ height: 25, width: "auto", marginRight: 5 }}
      />
    )
  }
];

const MainLogo = () => <img alt="" src={logo} style={{ width: 150 }} />;

class NavBarContainer extends Component {
  state = {
    visible: false,
    modalAbout: false
  };

  showModal = modal => this.setState({ [modal]: true });
  hideModal = modal => this.setState({ [modal]: false });

  test = () => alert("huehue");

  scrollToDiv = id => document.getElementById(id).scrollIntoView();

  leftMenu = () => (
    <React.Fragment>
      <Menu.Item
        as="a"
        onClick={this.scrollToDiv.bind(this, "div-hotel")}
        content="Hotel"
        key="hotel"
        icon={
          <img
            alt=""
            src="https://png.icons8.com/windows/50/000000/bed.png"
            style={{ height: 25, width: "auto", marginRight: 5 }}
          />
        }
      />
      <Menu.Item
        as="a"
        content="Products"
        key="products"
        icon={
          <img
            alt=""
            src="https://png.icons8.com/windows/50/000000/product.png"
            style={{ height: 25, width: "auto", marginRight: 5 }}
          />
        }
      />
    </React.Fragment>
  );

  rightMenu = () => (
    <React.Fragment>
      <Menu.Item
        as="a"
        content="About"
        key="about"
        onClick={this.showModal.bind(this, "modalAbout")}
      />
      <Menu.Item
        as="a"
        content="Contact Us"
        key="contact"
        onClick={this.scrollToDiv.bind(this, "div-contact")}
        icon={
          <img
            alt=""
            src="https://png.icons8.com/windows/50/000000/phone.png"
            style={{ height: 25, width: "auto", marginRight: 5 }}
          />
        }
      />
    </React.Fragment>
  );

  NavBarMobile = ({ children, onPusherClick, onToggle, visible }) => (
    <Sidebar.Pushable>
      <Sidebar
        as={Menu}
        animation="overlay"
        icon="labeled"
        items={leftItems}
        vertical
        visible={visible}
      />
      <Sidebar.Pusher
        dimmed={visible}
        onClick={onPusherClick}
        style={{ minHeight: "100vh" }}
      >
        <Menu fixed="top">
          <Menu.Item onClick={onToggle}>
            <Icon name="sidebar" />
          </Menu.Item>
          <Menu.Item>
            <MainLogo />
          </Menu.Item>
          <Menu.Menu position="right">
            <this.rightMenu />
          </Menu.Menu>
        </Menu>
        {children}
      </Sidebar.Pusher>
    </Sidebar.Pushable>
  );

  NavBarDesktop = () => (
    <Menu fixed="top">
      <Menu.Item>
        <MainLogo />
      </Menu.Item>
      <this.leftMenu />
      <Menu.Menu position="right">
        <this.rightMenu />
      </Menu.Menu>
    </Menu>
  );

  NavBarChildren = ({ children }) => (
    <Container style={{ marginTop: "5em" }}>{children}</Container>
  );

  handlePusher = () =>
    this.setState({ visible: this.state.visible ? false : true });

  handleToggle = () => this.setState({ visible: !this.state.visible });

  imgBanner = () => (
    <div
      style={{
        backgroundColor: "white",
        textAlign: "center",
        backgroundImage: `url(${imgBanner})`,
        backgroundSize: "cover",
        backgroundPosition: "center",
        backgroundRepeat: "no-repeat",
        height: 350,
        width: window.outerWidth,
        paddingTop: 110,
        position: "relative"
      }}
    >
      <img alt="" src={logoMain} style={{ height: 200, width: "auto" }} />
      <div
        style={{
          position: "absolute",
          bottom: 5,
          right: 50,
          display: "flex",
          justifyContent: "center"
        }}
      >
        <img
          alt=""
          src={imgWi}
          height={90}
          width="auto"
          style={{ marginRight: 10 }}
        />
        <img alt="" src={imgAsita} height={65} width="auto" />
      </div>
    </div>
  );

  render() {
    const { children } = this.props;
    const { visible } = this.state;

    return (
      <React.Fragment>
        <Responsive {...Responsive.onlyMobile}>
          <this.NavBarMobile
            onPusherClick={this.handlePusher}
            onToggle={this.handleToggle}
            visible={visible}
          >
            <this.NavBarChildren>{children}</this.NavBarChildren>
          </this.NavBarMobile>
        </Responsive>
        <Responsive minWidth={Responsive.onlyTablet.minWidth}>
          <this.NavBarDesktop />
          <this.imgBanner />
          <this.NavBarChildren>{children}</this.NavBarChildren>
        </Responsive>

        <Modal
          size="fullscreen"
          open={this.state.modalAbout}
          onClose={this.hideModal.bind(this, "modalAbout")}
        >
          <Modal.Header>About Us</Modal.Header>
          <Modal.Content>
            <p>{"Ares Tours & Travel"}</p>
          </Modal.Content>
          <Modal.Actions>
            <Button
              content="Close"
              onClick={this.hideModal.bind(this, "modalAbout")}
            />
          </Modal.Actions>
        </Modal>
      </React.Fragment>
    );
  }
}

export default NavBarContainer;



